/**
 * Used for testing rerending component scenario
 */
const useForceUpdate = () => {
  const [, set] = useState()
  return () => set({})
}

/**
 * Render counter
 */
const RenderCounter = () => {
  const renderCounter = useRef(0)
  renderCounter.current = renderCounter.current + 1

  return (
    <span className='container-info'>
      render count: {renderCounter.current}
    </span>
  )
}

/**
 * COMPONENT_1
 */
function AliceBox() {
  const [alice, setAlice] = useState(10)
  const forceUpdate = useForceUpdate()

  return (
    <div className='container column '>
      <span className='container-title'>component_1</span>
      <RenderCounter />
    </div>
  )
}

/**
 * main component aka APP
 */
function Demo() {
  const [alice, setAlice] = useState(10)
  const forceUpdate = useForceUpdate()

  return (
    <div className='container column demo'>
      <span className='container-title'>app</span>
      <RenderCounter />

      <div className='row desktop-right forced'>
        <button className='button'>Change state props</button>
        <button className='button' onClick={forceUpdate}>
          Rerender
        </button>
      </div>

      <TestProvider alice={alice}>
        <div className='row'>
          <AliceBox />
          <div className='container'></div>
        </div>
      </TestProvider>
    </div>
  )
}

export default Demo
